.scroll-top {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.icon-up-arrow {
  
  position: fixed;
  bottom: 13%;
  right: 3%;
  width: 70px;
  height: auto;
  fill: $accent-color;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
  cursor: pointer;

  &:hover {
    @media screen and (min-width: $desktop) {
      fill: $accent-hover-color;
      opacity: 1;
    }
  }
  &--active {
    opacity: 0.5;
    pointer-events: all;
  }
  &--not-visible {
    display: none;
  }
  @media screen and (max-width: $tablet) {
    right: 0;
  }
}
html {
  scroll-behavior: smooth;
}
.scroll-running {
  animation: animate 0.5s ease-out;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  @keyframes animate {
    0% {
      transform: translateY(0px);
    }
    70% {
      transform: translateY(-400px);
    }

    90% {
      transform: translateY(-600px);
    }
    100% {
      transform: translateY(-700px);
    }
  }
}

